/* -- GLOBAL -- */
html {
	box-sizing: border-box;
	overflow-y: scroll;
}

*, *:before, *:after {
	box-sizing: inherit;
}

/* -- Skip links -- */
.hide-skip {
	margin: 0;
}

.hide-skip a {
	color: black;
	position: absolute;
	overflow: hidden;
	height: 1px;
	z-index: 9999;
}

.hide-skip a:focus {
	height: auto;
}

/* -- layout -- */
.site-wrapper {
	max-width: 1400px;
	margin-left: auto;
	margin-right: auto;
	padding: 0 2em;
}

/* -- Links -- */
a {
	color: black;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

/* -- Base Typography -- */
body {
	line-height: 1.2em;
	font-family: 'Calibri', Tahoma, sans-serif;
}

.header {
	display: flex;
	flex-direction: column;
	margin-top: 70px;
}

.header ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.header__top {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

/* -- Logo -- */
.logo {
	margin: 0;
}

.logo__link {
	display: block;
	background-image: url("../img/header/logo.png");
	text-indent: 100%;
	white-space: nowrap;
	overflow: hidden;
	width: 311px;
	height: 38px;
}

/* -- About/contact -- */
.about-contact {
	display: flex;
}

.about-contact__link.current, .about-contact__link:hover {
	border-bottom: 1px solid black;
	text-decoration: none;
}

.about-contact__separator {
	margin: 0 2.5em;
}

/* -- Main Nav -- */
.main-nav {
	margin-top: 60px;
}

.main-nav__items {
	display: flex;
	justify-content: space-around;
}

.main-nav__link {
	display: block;
	background-image: url("../img/header/nav_sprite.png");
	text-indent: 100%;
	white-space: nowrap;
	overflow: hidden;
}

.main-nav__link--home {
	background-position: -3px -44px;
	width: 43px;
	height: 30px;
}

.main-nav__link--home:hover, .main-nav__link--home.current {
	background-position: -261px -44px;
}

.main-nav__link--health {
	background-position: -3px -80px;
	width: 240px;
	height: 32px;
}

.main-nav__link--health:hover, .main-nav__link--health.current {
	background-position: -261px -80px;
}

.main-nav__link--archive {
	background-position: -3px -152px;
	width: 121px;
	height: 33px;
}

.main-nav__link--archive:hover, .main-nav__link--archive.current {
	background-position: -261px -152px;
}

.main-nav__link--grow {
	background-position: -3px -187px;
	width: 95px;
	height: 33px;
}

.main-nav__link--grow:hover, .main-nav__link--grow.current {
	background-position: -261px -187px;
}

.main-nav__link--my {
	background-position: -3px -222px;
	width: 77px;
	height: 31px;
}

.main-nav__link--my:hover, .main-nav__link--my.current {
	background-position: -261px -222px;
}

.map {
	position: relative;
	margin: 60px auto;
	overflow: scroll;
	overflow: -moz-scrollbars-none;
	-ms-overflow-style: none;
	animation-name: fadeIn;
	animation-duration: 1.5s;
	animation-fill-mode: forwards;
	box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
	height: 600px;
	transition: transform 700ms ease-in-out;
	cursor: move;
	/* fallback: no `url()` support or images disabled */
	cursor: url("assets/img/icons/grab.cur"), move;
	/* fallback: Internet Explorer */
	cursor: grab;
}

.map::-webkit-scrollbar {
	display: none;
}

.map.dragscroll.grab {
	cursor: url("assets/img/icons/grab.cur"), move;
	cursor: grab;
}

.map.dragscroll.grabbing {
	cursor: url("assets/img/icons/grabbing.cur"), move;
	cursor: grabbing;
	transition: transform 700ms ease-in-out;
	box-shadow: none;
	border: none;
}

.map.dragscroll.grabbing .map__inner {
	transform: scale(0.8);
}

.map__inner {
	position: relative;
	transition: transform 700ms ease-in-out;
	background-image: url("../img/maps/map_today.png");
	background-repeat: no-repeat;
	width: 1336px;
	height: 939px;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.clickable {
	position: absolute;
	background-color: pink;
	height: 10.16260162601626016260162601626%;
	width: 19.571428571428571428571428571429%;
	left: 36.5%;
	top: 13.5%;
}

.clickable:hover {
	opacity: 0.8;
	cursor: pointer;
}

@media screen and (max-width: 960px) {
	.map.dragscroll.grabbing .map__inner {
		transform: scale(0.8);
	}
}
